<template>
  <div>
            <!--  -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/About' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
     </el-breadcrumb>

<!--  -->
<el-card>
    <el-row>
        <el-col>
            <el-button  type="primary" @click="addDialogVisible=true"></el-button>
        </el-col>
    </el-row>

     <el-table :data="rolelist" border stripe>

         <el-table-column type="expand">
             <template slot-scope="scope">
                 <el-row :class="['bdbottom', i1===0?'bdtop':'','vcenter']" v-for="(item1,i1) in scope.row.children" :key="item1.id">
                     <!--  -->
                     <el-col :span="5">
                        <el-tag>{{item1.authName}}</el-tag>  
                        <i class="el-icon-caret-right"></i>
                     </el-col> 
                     <!--  -->
                      <el-col :span="19">
                          <!--  -->
                          <el-row :class="[i2===0 ?'':'bdtop','vcenter']" v-for="(item2,i2) in item1.children" :key="item2.id">
                              <el-col :span="6">
                                   <el-tag type="success">{{item2.authName}}</el-tag>
                                     <i class="el-icon-caret-right"></i>
                              </el-col>
                              <el-col :span="18">
                                  <el-tag type="warning"  v-for="(item3,i3) in item2.children" :key="item3.id" closable @close='removetagId(scope.row,item3.id)'>
                                      {{item3.authName}}
                                  </el-tag>
                              </el-col>
                          </el-row>
                      </el-col>
                 </el-row>
                 <pre></pre>
             </template>
         </el-table-column>
           <el-table-column type="index" label="#"></el-table-column>
            
            <el-table-column label="" prop="roleName"></el-table-column>
             <el-table-column label="" prop="roleDesc"></el-table-column>
              <el-table-column label=""  width="350px">
                <template slot-scope="scope">
                      <el-button size="mini" type="primary"  icon="el-icon-edit"  @click="aa(scope.row.id)"></el-button>
                        <el-button size="mini" type="danger" icon="el-icon-delete"  @click="aa1(scope.row.id)"></el-button>
                          <el-button size="mini" type="warning"  icon="el-icon-setting" @click="showSetRighhtDialog(scope.row)"></el-button>
                               
                  </template>
              </el-table-column>
       </el-table>
</el-card>
<!--  -->
   <el-dialog
  title=""
  :visible.sync="setRightDialogVisible"
  width="50%"
  @close="setRightDialogClosed"
 >
<el-tree :data="rightslist" :props="treeProps" ref="treeRef" :default-checked-keys="defkeys" show-checkbox node-key="id" default-expand-all ></el-tree>
  <span slot="footer" class="dialog-footer">
    <el-button @click="setRightDialogVisible = false"> </el-button>
    <el-button type="primary" @click="alls()"> </el-button>
  </span>
</el-dialog>
   
 <!--  -->
    <el-dialog
        title=""
        :visible.sync="addDialogVisible "
        width="50%"
        @close="addDialogClosed"

        >
        <!--  -->
        <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="80px" class="demo-ruleForm">
            <el-form-item label=" " prop="roleName">
                <el-input v-model="addForm.roleName"></el-input>
            </el-form-item>
             <el-form-item label=" " prop="roleDesc">
                <el-input v-model="addForm.roleDesc"></el-input>
            </el-form-item>
           
        </el-form>
                <span slot="footer" class="dialog-footer">
            <el-button @click="addDialogVisible = false"> </el-button>
            <el-button type="primary" @click="addUser"> </el-button>
        </span>
        </el-dialog>
          <!--  -->
    <el-dialog
  title=""
  :visible.sync="editDialogVisidle"
  width="50%"
  @close="editDialogClosed"
 >
<el-form :model="editForm" :rules="editFormRules " ref="editFormRef" label-width="70px" >
  <el-form-item label=""  prop="roleName" >
    <el-input v-model="editForm.roleName" ></el-input>
  </el-form-item>
    <el-form-item label=""   prop="roleDesc">
    <el-input v-model="editForm.roleDesc" ></el-input>
  </el-form-item>

  </el-form>
  <span slot="footer" class="dialog-footer">
    <el-button @click="editDialogVisidle = false"> </el-button>
    <el-button type="primary" @click="editUserinfo"> </el-button>
  </span>
</el-dialog>
  </div>
</template>

<script>
export default {
    data(){
        return{
            defkeys:[],
            treeProps:{
                label:"authName",
                children:"children"
            },

            rolelist:[],
                     addForm:{
            roleName:'',
            roleDesc:''
         
        },
        roleId:"",
        //
        setRightDialogVisible:false,
        rightslist:[],
        editForm:{
            roleName:'',
            roleDesc:''
        },
        id:"",
        all:[],
        showEditDialog:false,
        editDialogVisidle:false,
                addDialogVisible:false,
                    addFormRules:{
            roleName:[
                {required:true,message:'',trigger:'blur'},
              
            ],
            roleDesc:[
                {required:true,message:'',trigger:'blur'},
        
            ]
            
        },
        editFormRules:{
            roleName:[
                {required:true,message:'',trigger:'blur'},
              
            ],
            roleDesc:[
                {required:true,message:'',trigger:'blur'},
        
            ]
        }
        }
    },
    created(){
        this.getRolesList()
    },
    methods:{
       async getRolesList(){
           const {data:res}= await this.$http.get("roles")
           if(res.meta.status !==200){
               return this.$message.error("")
           }
           this.rolelist=res.data
           console.log( this.rolelist)

        },
         addDialogClosed(){
          this.$refs.addFormRef.resetFields()

            },
            addUser(){
 this.$refs.addFormRef.validate(async valid=>{
              console.log(valid)
               if(!valid) return
               //
            const {data:res}= await this.$http.post("roles",this.addForm)
            if(res.meta.status !==201){
                this.$message.error("")

            }
            this.$message.success("")
            this.addDialogVisible=false
            this.getRolesList()
          })
            },
        async aa(e){
                console.log(e)
                this.id=e
          const {data:res} =await this.$http.get('roles/'+e)
          if(res.meta.status !==200){ 
              return this.$message.error("") 
          }
           this.editForm=res.data;
          this.editDialogVisidle=true
         
    // console.log(editForm)
                
            },
            editDialogClosed(){
                          this.$refs.editFormRef.resetFields()
            },
            editUserinfo(){

                  this.$refs.editFormRef.validate(async valid=>{
              console.log(valid) 
                  if(!valid) return 
                     //
            const {data:res}= await this.$http.put("roles/"+this.id,{
                roleName:this.editForm.roleName,
                roleDesc:this.editForm.roleDesc
            })
            if(res.meta.status !==200){
                return this.$message.error("")

            }
            //
            this.editDialogVisidle=false
            //
            this.getRolesList()
            this.$message.success("")

              })
           
            },
             async aa1(id){
       const res= await this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).catch(err=>err)
        // console.log(res)
        if(res !=="confirm"){
            return this.$message.info("")
        }
        const {data:res1}=await this.$http.delete("roles/"+id)
        if(res1.meta.status !==200){
            return this.$message.error("")
        }
        this.$message.success("")
                     this.getRolesList()

      },
    async removetagId(role,rightId ){

         console.log(rightId)
const res= await this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).catch(err=>err)
        // console.log(res)
        if(res !=="confirm"){
            return this.$message.info("")
        }else{
                     const {data:res1}=await this.$http.delete(`roles/${role.id}/rights/${rightId} `)
        if(res1.meta.status !==200){
            return this.$message.error("")
        }
                                    // this.getRolesList()
                 role.children=res1.data 
                            
        
        // this.$message.success("")
                
        }

      },

      async showSetRighhtDialog(role){
          this.roleId=role.id
          //
          const {data:res} =await this.$http.get('rights/tree')
          console.log(res)
          if(res.meta.status !==200){
              return this.$message.error("")
          }
          //  
          this.rightslist=res.data
          this.getLeafKeys(role,this.defkeys)
          this.setRightDialogVisible=true

      },
      getLeafKeys(node,arr){
          if(!node.children){
              return arr.push(node.id)
          }
          node.children.forEach(element => {
                this.getLeafKeys(element,arr)
          });
      },
      setRightDialogClosed(){
          this.defkeys=[]
      },
        async alls(){
          const keys=[...this.$refs.treeRef.getCheckedKeys(),
                    ...this.$refs.treeRef.getHalfCheckedKeys()]
                    const idStr=keys.join(",")
                   const {data:res}= await this.$http.post(`roles/${this.roleId}/rights`,{
                        rids:idStr
                    })
                    console.log(res)
                    if(res.meta.status !==200){
                        return this.$message.error("")
                    }
                    this.$message.success("")
                    this.getRolesList()
                    this.setRightDialogVisible=false
        
      }
    }
    
         
            
    
}
</script>

<style  scoped>
.el-tag{
    margin: 7px;
}
.bdtop{
    border-top: 1px solid #eee;
}
.bdbottom{
    border-bottom: 1px solid #eee;
}
.vcenter{
    display: flex;
    align-items: center;
}
</style>